.text {
    &-main {
        font-size: 2.7rem;
        font-weight: 700;
        letter-spacing: 30%;
        transition: all .2s ease;
        
        &:hover {
            transform: translateY(-2px);
            text-shadow: rgba(0,0,0,0.1) 0 1px 5px;
        }
    }
}


.despre-intro {
    &-title {
        font-size: 4rem;
        text-transform: uppercase;
        text-align: center;
        color: $purple;
        margin: 5rem 0;
    }

    &-p {
        font-size: 2.5rem;
        text-align: center;
        color: $purple;
        margin: 5rem 0;
        line-height: 3.5rem;

        &-pink {
            color: $pink;
        }

        &-blue {
            color: $blue;
        }

        &-purple {
            color: $purple;
        }
    }
}