@media only screen and (max-width: 1200px) {
    .slide {
        &-title {
            width: 100%;
        }
    }

    .o-right-bottom-text-2 {
        left: -1.25rem;
    }

    .navButton {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
    }

    .linksHolder {
        display: none;
    }

    .card-membru {
        flex-direction: column;
    }

    .membru {
        &-img {
            margin-right: 0;
        }

        &-nume {
            transform: translate(-50%, 0);
        }
        
        &-img:hover + &-nume {
            transform: translate(-50%, 50%);
        }
    }

    .geo-fix {
        margin-left: 0;
    }
}

@media only screen and (max-width: 900px) {
    .slide {
        &-controls {
            width: 100%;
        }

        &-social {
            bottom: -22.7rem;
        }
    }

    .slider {
        background-position-y: 50%;
    }

    .container {
        padding-bottom: 30rem;
    }

    .o-left-bottom-social-3 {
        bottom: -24.1rem;
    }

    .o-right-bottom-controls-2 {
        bottom: -24.1rem;
        left: -1.25rem;
    }

    .o-right-bottom-text-3 {
        display: none;
    }
}

@media only screen and (max-width: 600px) {
    .container {
        padding-left: 7rem;
        padding-right: 7rem;
    }

    .slider {
        height: 65rem;
        background-position-y: -5rem;
    }

    .despre-intro-p {
        font-size: 1.75rem;
    }
}

@media only screen and (max-width: 500px) {
    .slider {
        height: 45rem;
    }
}


/////////////// chrome fix /////////////////

@media screen and (-webkit-min-device-pixel-ratio:0) and (min-resolution:.001dpcm) {
    .outline {
        &-purple {
            border: solid .75rem $purple;
            outline: none;
        }
    
        &-pink {
            border: solid .75rem $pink;
            outline: none;
        }
    
        &-blue {
            border: solid .75rem $blue;
            outline: none;
        }
    }
}

@-moz-document url-prefix() {
    .outline {
        &-purple {
            outline: solid .75rem $purple;
            border: none;
        }
    
        &-pink {
            outline: solid .75rem $pink;
            border: none;
        }
    
        &-blue {
            outline: solid .75rem $blue;
            border: none;
        }
    }
}

